http://prismjs.com/download.html?themes=prism-okaidia&languages=markup+css+clike+javascript+scss&plugins=line-numbers+toolbar+show-language+copy-to-clipboard */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

code[class*="language-"],
pre[class*="language-"] {
  color           : #F8F8F2;
  background      : none;
  text-shadow     : 0 1px rgba(0, 0, 0, 0.3);
  font-family     : Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
  border          : none;
  text-align      : left;
  white-space     : pre;
  word-spacing    : normal;
  word-break      : normal;
  word-wrap       : normal;
  line-height     : 1.5;
  -moz-tab-size   : 4;
  -o-tab-size     : 4;
  tab-size        : 4;
  -webkit-hyphens : none;
  -moz-hyphens    : none;
  -ms-hyphens     : none;
  hyphens         : none;
}

/* Code blocks */
pre[class*="language-"] {
  padding       : 1em;
  overflow      : auto;
  border-radius : 0.3em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
  background : #272822;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
  padding       : .1em;
  border-radius : .3em;
  white-space   : normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color : slategray;
}

.token.punctuation {
  color : #F8F8F2;
}

.namespace {
  opacity : .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
  color : #F92672;
}

.token.boolean,
.token.number {
  color : #AE81FF;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color : #A6E22E;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
  color : #F8F8F2;
}

.token.atrule,
.token.attr-value,
.token.function {
  color : #E6DB74;
}

.token.keyword {
  color : #66D9EF;
}

.token.regex,
.token.important {
  color : #FD971F;
}

.token.important,
.token.bold {
  font-weight : bold;
}

.token.italic {
  font-style : italic;
}

.token.entity {
  cursor : help;
}

pre.line-numbers {
  position      : relative;
  padding-left  : 3.8em;
  counter-reset : linenumber;
}

pre.line-numbers > code {
  position    : relative;
  white-space : inherit;
}

.line-numbers .line-numbers-rows {
  position            : absolute;
  pointer-events      : none;
  top                 : 0;
  font-size           : 100%;
  left                : -3.8em;
  width               : 3em; /* works for line-numbers below 1000 lines */
  letter-spacing      : -1px;
  border-right        : 1px solid #999;
  -webkit-user-select : none;
  -moz-user-select    : none;
  -ms-user-select     : none;
  user-select         : none;
}

.line-numbers-rows > span {
  pointer-events    : none;
  display           : block;
  counter-increment : linenumber;
}

.line-numbers-rows > span:before {
  content       : counter(linenumber);
  color         : #999;
  display       : block;
  padding-right : 0.8em;
  text-align    : right;
}

pre.code-toolbar {
  position : relative;
}

pre.code-toolbar > .toolbar {
  position   : absolute;
  top        : .3em;
  right      : .2em;
  transition : opacity 0.3s ease-in-out;
  opacity    : 0;
}

pre.code-toolbar:hover > .toolbar {
  opacity : 1;
}

pre.code-toolbar > .toolbar .toolbar-item {
  display : inline-block;
}

pre.code-toolbar > .toolbar a {
  cursor : pointer;
}

pre.code-toolbar > .toolbar button {
  background          : none;
  border              : 0;
  color               : inherit;
  font                : inherit;
  line-height         : normal;
  overflow            : visible;
  padding             : 0;
  -webkit-user-select : none; /* for button */
  -moz-user-select    : none;
  -ms-user-select     : none;
}

pre.code-toolbar > .toolbar a,
pre.code-toolbar > .toolbar button,
pre.code-toolbar > .toolbar span {
  color         : #BBB;
  font-size     : .8em;
  padding       : 0 .5em;
  background    : #F5F2F0;
  background    : rgba(224, 224, 224, 0.2);
  box-shadow    : 0 2px 0 0 rgba(0, 0, 0, 0.2);
  border-radius : .5em;
}

pre.code-toolbar > .toolbar a:hover,
pre.code-toolbar > .toolbar a:focus,
pre.code-toolbar > .toolbar button:hover,
pre.code-toolbar > .toolbar button:focus,
pre.code-toolbar > .toolbar span:hover,
pre.code-toolbar > .toolbar span:focus {
  color           : inherit;
  text-decoration : none;
}

